.header {
    color: white;
    display: flex;
    padding: 20px;
    align-items: center;
    justify-content: space-between;
    overflow-x: scroll;
}

.header::-webkit-scrollbar {
    display: none;
}

.header_icon:hover > p {
    display: inline-flex;
}


.header_icon--active > p {
    display: inline-flex !important;
}

.header_icon > .MuiSvgIcon-root {
    margin-left: auto;
    margin-right: auto;
    font-size: 30;
}

.header_icon > p {
    display: none;
    text-transform: uppercase;
    letter-spacing: 3px;
    margin-left: auto;
    margin-right: auto;
    font-size: small;
}

.header_icon {
    display: flex;
    flex-direction: column;
    cursor: pointer;
    width: 100px;
    margin-right: 30px;
}

.header_icons {
    display: flex;
    align-items: center;
    text-align: center;
}


.header > img {
    object-fit: contain;
    height: 80px;
}


    